@charset "UTF-8";

.container {
    width: min(100% - 40px, 1080px);
    margin-inline: auto;
}

body {
    font-family: "Inter", Arial, sans-serif;
    color: rgb(255, 255, 255);
}

.flex {
    display: flex;
}

.sb {
    justify-content: space-between;
}

.gap {
    gap: 20px;
}

/* MAIN */
main {
    width: 100%;
    height: 100dvh;
    display: grid;
    place-items: center;
}

.phone {
    width: 331px;
    height: 700px;
    border-radius: 30px;
    background-image: url(../media/bg.jpg);
    background-position: center;
    background-size: cover;
}

.glass {
    backdrop-filter: blur(24px) saturate(80%);
    width: 331px;
    height: 700px;
    border-radius: 30px;
    border: 1px solid rgba(255, 255, 255, 0.089);
    position: relative;
    padding: 110px 0;
    display: grid;
    place-items: center;
}

.home {
    position: absolute;
    height: 4px;
    border-radius: 2px;
    width: 45%;
    bottom: 10px;
    left: 91px;
    background-color: rgba(255, 255, 255, 0.7);
}

.numpad {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 18px; 
    & > :last-child {
        grid-column: 2 / 3;
    }
}

input {
    color: rgb(255, 255, 255);
    width: 160px;
    height: 43px;
    text-align: center;
    border-radius: 100px;
    background-color: rgba(255, 255, 255, 0.322);
    border: 1px solid rgba(255, 255, 255, 0.6);
    pointer-events: none;
    &::placeholder {
        color: rgb(255, 255, 255);
    }
}

button {
    width: 70px;
    height: 70px;
    background-color: rgba(255, 255, 255, 0.322);
    border: 1px solid rgba(255, 255, 255, 0.6);
    transition-duration: 0.2s;
    border-radius: 50%;
    p {
        font-size: 29px;
        color: rgba(255, 255, 255, 0.82);
        font-weight: 300;
        transform: scaleX(1.1);
    }
    &:active {
        background-color: rgba(255, 255, 255, 0.445);
    }
}